.backdrop {
    display: none;
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 1;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.2);
  }
  .backdrop.is-hidden {
    opacity: 0;
    pointer-events: none;
    visibility: hidden;
  }
  
  .modal {
    position: relative;
    margin: 15% auto;
    padding: 20px;
    width: 280px;
    height: 998px;
    background-color: #fff;
  }
  .modal-btn-close {
    position: absolute;
    cursor: pointer;
    top: 8px;
    right: 8px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
    background-color: #fff;
    padding: 8px;
  }
  .icon-modal {
    fill: black;
    width: 14px;
    height: 14px;
  }